@import "../../../global.scss";

.contact {
    background-color: whitesmoke;
    display: flex;

    @include mobile {
        flex-direction: column;
    }

    .left {
        flex: 1;
        overflow: hidden;

        img {
            height: 100%;
        }
    }
    
    .right {
        flex: 1;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;

        h2 {
            font-size: 40px;

            @include mobile {
                font-size: 25px;
            }
        }

        form {
            width: 700px;
            height: 700px;
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: space-around;

            @include mobile {
                height: 350px;
            }

            input {
                width: 300px;
                height: 30px;
                font-size: 14px;

                @include mobile {
                    width: 200px;
                    height: 20px;
                }
            }

            textarea {
                width: 300px;
                height: 200px;
                font-size: 14px;

                @include mobile {
                    width: 200px;
                    height: 100px;
                }
            }

            button{
                width: 150px;
                height: 30px;
                color: white;
                background-color: black;
                border: none;
                border-radius: 10px;
                font-weight: 500;
                cursor: pointer;

                &:focus {
                    outline: none;
                }
            }

            span{
                color: blue;
            }
        }
    }
}