@import "../../../global.scss";

.portfolio{
    background-color: whitesmoke;
    display: flex;
    flex-direction: column;
    align-items: center;

    h1{
        font-size: 50px;
        //height: 100px;

        @include mobile {
            font-size: 20px;
        }
    }

    ul{
        margin: 10px;
        padding: 0;
        list-style: none;
        display: flex;
    
        @include mobile {
            margin: 10px 0;
            flex-wrap: warp;
            justify-content: center;
        }
    }

                
    .container{
        width: 1500px;
        height: 1000px;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-wrap: wrap ;

        @include mobile {
            width: 100%;
            height: 50%;
            
        }

        .item{
            width: 75%;
            height: 75%;
            border-radius: 20px;
            border: 1px solid white;
            margin: 10px 20px;
            display: flex;
            align-items: center;
            justify-content: center;
            color: white;
            position: relative;
            transition: all 0.5s ease;
            cursor: pointer;

            @include mobile {
                width: 130px;
                height: 100px;
            }

            h3{
                position: absolute;
                font-size: 20px;
            }

            img{
                width: 100%;
                height: 100%;
                object-fit: cover;
                z-index: 1;
            }

            &:hover{
                background-color: $mainColor;
                img {
                    opacity: 0.2;
                    z-index: 0;
                }
            }
        }
    }
}